import emailjs from 'emailjs-com';
import React, { useRef } from 'react';


const Contact = () => {
    const form = useRef();
    function sendEmail(e){
        alert("Email Sent Thank you");
        e.preventDefault();

        emailjs.sendForm('service_8l2bzan','template_4vlftsk',form.current,'Rml5Y3Eznyte8CIF5')
            .then ((result)=>{
                console.log(result.text);
                
            },(error)=>{
                console.log(error.text);
            });
            e.target.reset();
    }
    return (
        <>

            <section className="container-fluid bg-metal " id="cm">

                <div className="row g-0">

                    <div data-aos = "fade-left"  className="col-lg-6  h-100  d-flex flex-column p-5 ">
                        <div className=" ">
                            <h2 data-aos="fade-in" className="  display-6  text-white"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="35" fill="currentColor" class="bi bi-chat-left-dots" viewBox="0 0 16 16">
                                <path d="M14 1a1 1 0 0 1 1 1v8a1 1 0 0 1-1 1H4.414A2 2 0 0 0 3 11.586l-2 2V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12.793a.5.5 0 0 0 .854.353l2.853-2.853A1 1 0 0 1 4.414 12H14a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z" />
                                <path d="M5 6a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm4 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm4 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
                            </svg><span className="px-2 text-light">Contact</span> <b> Me</b> </h2>

                        </div>
                        <div className="display-1 py-4 ">
                            For feedbacks, suggestions or queries
                        </div>
                        <p>
                            Feel free to leave me a message
                        </p>

                    </div>
                    <div data-aos = "fade-left" className = "col-lg-6   px-5 py-3 ms-auto ">
                        <form ref = {form} onSubmit={sendEmail} className="d-flex flex-column">
                            
                            <input type = "text" className="my-3 border border-bottom-1 border-top-0 border-end-0 border-start-0 con-inp" placeholder="Enter Name" name = "name"/>
                            <input type= "email" name = "email" className="my-3 border border-bottom-1 border-top-0 border-end-0 border-start-0 con-inp" placeholder="Enter Email"/>
                        
                            <textarea name = "message" style= {{height:'250px'}} className="my-3 border border-bottom-1 border-top-0 border-end-0 border-start-0 con-inp" placeholder="Enter Comment"></textarea>

                            <input type = "submit" value ="Submit" className="ms-auto btn btn-outline-light"/>
                                                            
                        </form>

                    </div>
                </div>


            </section>
        </>

    );
}
export default Contact;