import { useState } from 'react';
import { Nav, Navbar, Container } from 'react-bootstrap';
import { Link } from 'react-scroll'
import '../styling/styling.css';
import React, { Component } from "react";
import ScrollspyNav from "react-scrollspy-nav";



const NavBar = () => {
    const [navbar, setNavbar] = useState(false);
    const [click, setClick] = useState(false);
    const handleClick = () => setClick(!click);
    const closeMenu = () => setClick(false);


    const changeNavBg = () => {
        if (window.scrollY > 0) {
            setNavbar(true);
        }
        else {
            setNavbar(false);
        }
    }


    window.addEventListener('scroll', changeNavBg);

    return (
        <>

            <Navbar collapseOnSelect className={navbar ? 'bg-theme' : 'none'} sticky="top" variant={navbar ? 'light' : 'dark'} expand='lg'  >


                <Container className="px-lg-5 px-md-0">
                    <Navbar.Brand className='bg-dark metalic-brand text-white px-4 ms-lg-5 ms-md-0 ' href="#home">ADS</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">


                        <Nav className="ms-auto nav-menu" >
                           
                            <li className='nav-item'>
                                <Link className={navbar ? 'mx-lg-3 mx-md-0   bg-less' : 'mx-lg-3 mx-md-0   bg-with'} spy={true} smooth={true} offset={-100} duration={500} onClick={closeMenu} eventKey={2} to="projects">
                                    Projects
                                </Link>
                            </li>
                           
                            <li className='nav-item'>
                                <Link spy={true} smooth={true} offset={-100} duration={500} onClick={closeMenu} className={navbar ? 'mx-lg-3 mx-md-0  bg-less' : 'mx-lg-3 mx-md-0   bg-with'} eventKey={3} to="snk">
                                    Skillset
                                </Link>
                            </li>
                            <li className='nav-item '>
                                <Link spy={true} smooth={true} offset={-100} duration={500} onClick={closeMenu} className={navbar ? 'mx-lg-3 mx-md-0  bg-less' : 'mx-lg-3 mx-md-0  bg-with '} to="abt">About Me</Link>
                            </li>
                            <li className='nav-item'>
                                <Link spy={true} smooth={true} offset={-100} duration={500} onClick={closeMenu} className={navbar ? 'mx-lg-3 mx-md-0   bg-less' : 'mx-lg-3 mx-md-0   bg-with'} eventKey={4} to="cm">
                                    Contact Me
                                </Link>

                            </li>

                        </Nav>

                    </Navbar.Collapse>
                </Container>

            </Navbar>


        </>

    );




}
export default NavBar

