import React, { Component } from "react";
import Slider from "react-slick";


const inter1 = require("../resources/inter-1.jpg");
const inter2 = require("../resources/inter-2.png");
const inter3 = require("../resources/inter-3.png");
const inter4 = require("../resources/inter-4.jpeg");
const inter5 = require("../resources/bg-comic.jpeg");

export default class VerticalMode extends Component {
    render() {
        const settings = {
            dots: true,
            infinite: true,
            slidesToShow: 4,
            centerMode: false,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 1000,
            vertical:true,
           
            focusOnSelect: true,

            responsive: [
                {
                    breakpoint: 1250,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        centerMode: false,
                        infinite: true,
                        dots: true,
                        vertical: false,
                        centerPadding: "0px",
                    }
                },
                {
                    breakpoint: 600, 
                   
                    settings: {
                        centerMode: false,
                        slidesToShow: 1,
                        verticalSwiping: false,
                        vertical: false,
                        slidesToScroll: 1,
                        initialSlide: 1
                    }
                },
                {
                    breakpoint: 480,
                  
                    settings: {
                        centerMode: false,
                        slidesToShow: 1,
                        verticalSwiping: false,
                        vertical: false,
                        slidesToScroll: 1,
                        initialSlide: 1,
                        dots: false,
                    }
                },
                {
                    breakpoint: 360,
                    vertical: false,
                    settings: {
                        verticalSwiping: false,
                        vertical: false,
                        centerMode: false,
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        initialSlide: 1,
                        dots: false,
                        
                    }
                }
            ],
          
            beforeChange: function (currentSlide, nextSlide) {
                console.log("before change", currentSlide, nextSlide);
            },
            afterChange: function (currentSlide) {
                console.log("after change", currentSlide);
            }

        };
        return (
            <div  data-aos = "fade-up" className=" col-lg-3  col-md-12  ms-auto  " >
                <h4>Here are my Interests</h4>
                <div className="">
                    <Slider {...settings}>
                        
                        <div>
                            <img src={inter1} className="img-fluid abt-img"  alt = "mma" title= "MMA"/>
                        </div>
                        <div>
                            <img src={inter2} className="img-fluid abt-img"  alt = "Nirvana" title= "Nirvana" />
                        </div>
                        <div>
                            <img src={inter3} className="img-fluid abt-img" alt = "The weeknd" title= "The weeknd" />
                        </div>
                        <div>
                            <img src={inter4} className="img-fluid abt-img"  alt = "working out" title= "Working Out"/>
                        </div>
                        <div>
                            <img src={inter5} className="img-fluid abt-img" alt = "Comics" title= "Comics" />
                        </div>
                   
                    </Slider>
                </div>
            </div>
        );
    }
}